[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  background: linear-gradient(13deg, #ff8f5a 26%, #ff2f96 72%);
  -webkit-background-clip: text;
  color: transparent;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: .2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-phone__header:before {
  content: '\e800';
  font-size: 0; }

.icon-phone__footer:before {
  content: '\e800'; }

.icon-mail-alt:before {
  content: '\f0e0'; }

@media (max-width: 750px) {
  .icon-phone__header:before {
    font-size: 20px; } }

