* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-family: Roboto, sans-serif;
  color: #1f1f24; }

ul {
  list-style: none; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }

.conteiner {
  max-width: 1210px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px; }

.header__button {
  width: 206px;
  height: 45px;
  border: 1px solid #ff2f96;
  background-color: transparent;
  color: #ff2f96;
  transition: background 0.2s, color 0.2s; }
  .header__button:hover {
    background-color: #ff2f96;
    color: #fff; }

.header__conteiner {
  background-color: #fbfbfb; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 103px; }
  .header__button {
    align-items: center; }
  .header__phone {
    margin-left: auto;
    align-items: center;
    font-size: 25px;
    color: #1f1f24;
    margin-right: 26px;
    transition: color 0.2s; }
    .header__phone:hover {
      color: #ff2f96; }

.menu {
  display: flex; }
  .menu__item {
    margin-right: 32px; }
  .menu__link {
    transition: color 0.2s;
    font-weight: 400;
    color: #1f1f24; }
    .menu__link:hover {
      color: #ff2f96;
      border-bottom: 1px solid #ff2f96; }

.burger {
  display: none;
  background-color: transparent;
  position: relative;
  height: 52px;
  width: 60px;
  border: none;
  cursor: pointer; }
  .burger__btn span {
    left: 6px;
    top: 23px; }
  .burger__btn span, .burger__btn span::before, .burger__btn span::after {
    position: absolute;
    width: 48px;
    height: 6px;
    transition: width 0.4s, transform 0.4s, top 0.4s;
    background: linear-gradient(13deg, rgba(255, 143, 90, 0.9) 26%, rgba(255, 47, 150, 0.95) 72%); }
  .burger__btn span::before, .burger__btn span::after {
    content: '';
    left: 0; }
  .burger__btn span::before {
    top: 12px; }
  .burger__btn span::after {
    top: -12px; }
  .burger__btn_click span {
    width: 0;
    margin-left: 0; }
  .burger__btn_click span::after {
    transform: rotate(45deg);
    margin-top: 0;
    top: 0; }
  .burger__btn_click span::before {
    transform: rotate(-45deg);
    margin-top: 0;
    top: 0; }

@media screen and (max-width: 1024px) {
  .burger {
    display: block; }
  .header__nav_phone {
    position: absolute;
    top: 0;
    left: -50vh;
    width: 50vh;
    height: 100vh;
    transition: transform 0.3s linear;
    background-color: rgba(255, 47, 150, 0.97); }
  .menu__link_phone {
    color: #fff;
    font-size: 20px; }
  .header__nav_click {
    transform: translateX(50vh); }
  .menu__phone {
    flex-direction: column;
    margin-top: 50px; }
  .menu__item_phone {
    margin: 20px auto; } }
